#quickMenu,
.cont_top_wrap,
.board_view_top,
.board_view_section + .btn_wrap, .btn_wrap {
  display: none;
}

.board_view_section {
  border-top: none;
}

.sub_contents {
  width: 100%;
}

.news_cont .board_view_section {
  margin-top: 0px;
}
.board_view_content {
  padding: 0px;
}

.container {
  padding-bottom: 0px;
  padding-top: 78px;
